<template>
  <div class="flex items-start justify-center h-screen w-screen overflow-y-auto px-10">
    <Card
      v-loading="loading.all"
      :class="[
        'my-auto',
        dataRoute.isExist && 'w-full max-w-2xl rounded-lg border border-grey-01 border-opacity-50 h-auto',
        !dataRoute.isExist && 'w-full xl:w-1/3 md:1/2 lg:w-1/2 py-6 px-8'
      ]">
      <div v-if="!dataRoute.isExist" class="flex justify-center items-center flex-col mb-12">
        <img src="@/assets/logo.svg" alt="Brand logo" whitelabel="image-replace" data-image-type="logo" class="mb-4" width="180">
        <p class="text-dark">Create your account</p>
      </div>
      <form @submit.prevent="handleSubmit">
        <div v-if="!dataRoute.isExist" class="grid grid-cols-2 gap-6">
          <div class="xl:col-span-1 lg:col-span-1 md:col-span-1 col-span-2">
            <Input v-model="formData.first_name" :view="validation.first_name.$invalid ? 'danger' : 'default'" placeholder="First Name" block />
            <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.first_name.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="xl:col-span-1 lg:col-span-1 md:col-span-1 col-span-2">
            <Input v-model="formData.last_name" :view="validation.last_name.$invalid ? 'danger' : 'default'" placeholder="Last Name" block />
            <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.last_name.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="col-span-2">
            <Input v-model="formData.job_title" :view="validation.job_title.$invalid ? 'danger' : 'default'" placeholder="Job Title" block />
            <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.job_title.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="col-span-2">
            <Input v-model="email" placeholder="Email" block disabled />
          </div>
          <div class="col-span-2">
            <Input v-model="formData.password" :type="isShow.password ? 'text' : 'password'" :view="validation.password.$invalid ? 'danger' : 'default'" placeholder="Password" block autocomplete="new-password" icon right>
              <template #icon>
                <span class="material-icons block cursor-pointer text-grey-03 select-none" @click="isShow.password = !isShow.password">{{ isShow.password ? 'visibility_off' : 'visibility' }}</span>
              </template>
            </Input>
            <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.password.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="col-span-2">
            <Input v-model="formData.password_confirmation" :type="isShow.confirmPassword ? 'text': 'password'" :view="validation.password_confirmation.$invalid ? 'danger' : 'default'" placeholder="Confirm Password" block autocomplete="new-password" icon right>
              <template #icon>
                <span class="material-icons block cursor-pointer text-grey-03 select-none" @click="isShow.confirmPassword = !isShow.confirmPassword">{{ isShow.confirmPassword ? 'visibility_off' : 'visibility' }}</span>
              </template>
            </Input>
            <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.password_confirmation.$errors" :key="errorIndex">{{ error.$message }}. </small>
          </div>
          <div class="col-span-2">
            <Button color="teal" block :loading="loading.submit">Register</Button>
          </div>
        </div>
        <div v-else class="-m-6">
          <div class="border-b border-grey-01 px-6 py-6">
            <h1 class="text-xl">{{ dataRoute.company }}</h1>
          </div>
          <div class="px-6 py-6">
            You've been invited to join the {{ dataRoute.company }} team by
            <p class="font-semibold mb-4">{{ dataRoute.inviteBy }}</p>
            <div
              v-if="alertRecognize"
              class="bg-orange-01 rounded-lg px-4 py-2 flex gap-x-4 items-center relative mb-4">
              <span class="material-icons text-4xl text-orange-04">info</span>
              <div class="flex items-center gap-x-4 text-orange-04">
                Don't recognize this email address?
                <a href="https://www.reusely.com" target="_blank" class="text-grey-base hover:text-grey-base border-b border-opacity-0 border-grey-base hover:border-opacity-100">Contact Us</a>
              </div>
              <button class="absolute right-2" @click="alertRecognize = false">
                <span class="material-icons text-4xl text-orange-04">close</span>
              </button>
            </div>
          </div>
          <div class="border-t border-grey-01 px-6 py-6">
            <div class="flex items-center justify-between gap-x-4">
              <router-link
                to="/login"
                class="hover:text-red-base text-grey-base">
                Cancel
              </router-link>
              <Button color="teal" type="submit" view="smooth">
                Join Team
              </Button>
            </div>
          </div>
        </div>
      </form>
      <div v-if="!dataRoute.isExist" whitelabel="remove" class="text-center my-6">
        <p>By clicking "Register" you agree to reusely.com's <a href=" https://www.reusely.com/terms-of-service" rel="noreferrer noopener">Terms and Conditions</a> and <a href="https://www.reusely.com/privacy-policy" rel="noreferrer noopener">Privacy Policy</a></p>
      </div>
    </Card>
  </div>
  <Modal
    v-model="modalSucceed"
    size="xs"
    hide-header>
    <div class="flex flex-col gap-4 py-10 justify-center items-center">
      <span
        style="font-size: 10rem;"
        class="material-symbols-outlined text-green-02">
        check_circle
      </span>
      <div class="text-center text-2xl font-semibold">
        Welcome to the team!
        <p class="text-base font-normal mt-4">
          Your're now viewing details for {{ dataRoute.company }}. You can also access another account.
        </p>
      </div>
      <div class="flex gap-x-4 justify-between items-center">
        <Button
          color="teal"
          @click.prevent="handleSignIn">
          {{ 'Sign In' }}
        </Button>
      </div>
    </div>
  </Modal>
</template>

<script>
import { computed, defineComponent, onMounted, reactive, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useVerifyInviteUser } from 'composable/user'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import { helpers } from '@vuelidate/validators'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'InviteVerify',
  components: {
    Button,
    Card,
    Input,
    Modal
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const { formData, loading, action, actionVerifyInvite } = useVerifyInviteUser()
    const dataRoute = computed(() => ({
      isExist: route.query.is_existing
        ? Number(route.query.is_existing)
          ? true : false
        : false,
      company: route.query.company_name ? route.query.company_name : 'Unknown Company',
      inviteBy: route.query.invite_by ? route.query.invite_by : 'Unknown User'
    }))
    const alertRecognize = ref(true)
    const isShow = reactive({
      password: false,
      confirmPassword: false
    })
    const token = ref('')
    const email = ref('')
    const modalSucceed = ref(false)
    const rules = {
      first_name: {
        required
      },
      last_name: {
        required
      },
      job_title: {
        required
      },
      password: {
        required,
        validation: helpers.withMessage('At least 6 characters and include capital letters', helpers.regex(/^(?=.*?[A-Z]).{6,}$/))
      },
      password_confirmation: {
        required,
        sameAs: helpers.withMessage("Confirmation password didn't match", () => formData.password === formData.password_confirmation)
      }
    }
    const validation = useVuelidate(rules, formData, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleSubmit = async () => {
      const isValid = dataRoute.value.isExist ? true : validate()
      if(isValid) await action(token.value, dataRoute.value.isExist, () => {
        if (!dataRoute.value.isExist) {
          ElMessage({
            type: 'success',
            message: 'Account registered successfully'
          })
          router.push({
            name: 'login'
          })
        } else modalSucceed.value = true
      })
    }
    const handleSignIn = () => {
      router.push({
        name: 'login'
      })
    }
    
    onMounted(async () => {
      const queryToken = route.query.token || ''
      if(!queryToken) return router.push({
        name: 'login'
      })
      token.value = queryToken
      await actionVerifyInvite(token.value, (response) => {
        const responseEmail = response?.email || ''
        email.value = responseEmail
      }, () => {
        router.push({
          name: 'login'
        })
      })
    })

    return {
      route,
      router,
      dataRoute,
      modalSucceed,
      alertRecognize,
      isShow,
      validation,
      formData,
      loading,
      email,
      handleSignIn,
      handleSubmit
    }
  },
})
</script>